@import "_transition";

.faq {
  @include           transition;
  max-width        : 80%;
  margin           : auto;
  margin-bottom    : 1rem;
  padding          : 1rem 2rem;
  border           : none;
  border-radius    : 4px;
  background-color : $accent-shade;

  &:hover {
    @include           transition;
    color            : $light-color;
    background-color : $accent-color;
  }

  h3 {
    margin-bottom  : 0;
    font-weight    : $light_font;
    letter-spacing : 2px;
  }

  li,
  p {
    margin-top    : 1rem;
    margin-bottom : .3rem;
    font-family   : $paragraph-font-family;
    list-style    : none;
  }
}

.faq-q.close {
  cursor : pointer;

  &::after {
    float       : right;
    font-size   : 11px;
    font-weight : 300;
    line-height : 30px;
  }

  &::before {
    -moz-transform    : rotate(-90deg)!important;
    -ms-transform     : rotate(-90deg)!important;
    -o-transform      : rotate(-90deg)!important;
    -webkit-transform : rotate(-90deg)!important;
    transform         : rotate(-90deg)!important;
  }
}

