// Main SCSS file
@import "_resets";
@import url('https://fonts.googleapis.com/css?family=Megrim&display=swap');
@import url('https://fonts.googleapis.com/css?family=Gotu&display=swap');

$primary-color: #479aa1;
$primary-shade: lighten($primary-color, 30);
$accent-color: #f5aea8;
$accent-shade: lighten($accent-color, 10);
$dark-color: #707070;
$light-color: #f1f1f1;

$header-font-family: 'Megrim', cursive;;
$paragraph-font-family: 'Gotu', sans-serif;;

$light_font: 200;
$regular_font: 400;
$bold_font: 600;
$extra_bold_font: 700;

// font sizes
$heading1-font-size: 3rem;
$heading2-font-size: 2rem;
$heading3-font-size: 2rem;
$paragraph-font-size: 1rem;
$paragraph-line-height: 1.7;
$cta-button-font-size: 1rem;
$learn-more-button-font-size: .7rem;
$button-border-radius: 5px;

@import "_universal",
        "_utility",
        "_navbar",
        "_buttons",
        "_header",
        "_footer",
        "_intro-section",
        "_triple-features",
        "_upcoming-retreat",
        "_community-section",
        "_schedule-calendar",
        "_info-box",
        "_faqs",
        "_testimonials",
        "_all-retreats",
        "_cta-section",
        "_about",
        "_offerings",
        "_community-page",
        "_blog",
        "_media-queries";
